import { USER_API } from "@/api/client";
import {debounce} from "../../../tools/common";
import { mapState } from "vuex";
export default {
  name: "cbkbexchange",
  data(){
    return{
      airDropCount: 0,
      src: 'https://cn.etherscan.com/address/0xa286035a1e60abf172524bdbfd224abeef6ce362',
      flag: false,
      isLoading: true,
      status: false,
    }
  },
  computed: {
    ...mapState(["local"]),
    locale() {
      return this.local.locale;
    },
    isBitKeep() {
      return this.local.UA.isBitKeep;
    }
  },
  async created(){
    process.client && window.addEventListener('load',()=>{
      this.getBalance();
    });
    await this.$nextTick();
    this.isLoading = false;
  },
  beforeMount() {
    this.isBitKeep &&
    BitKeepInvoke.setTitle(this.$t("airdrop.airdropTitle"));
  },
  methods:{
    // 获取地址cbkb地址
    async getBalance() {
      if(!window.ethereum){
        return
      }else{
        await window.ethereum.request({ method: "eth_requestAccounts" });
        this.getAirDropCount(window.ethereum.selectedAddress);
      }
    },
    async getAirDropCount(address){
      const { data, status } = await USER_API.getAirDropCount({
        address,
        lang: this.local.locale
      });
      if (status == 1){
        return this.$dialog.alert({ message: data,confirmButtonText: this.$t('CbkbExchange.know'),confirmButtonColor: '#7524f9' });
      }
      this.airDropCount = this.milliFormat(data.amount)
      this.status = data.status;
    },
    milliFormat (num) {
      return num && num.toString()
        .replace(/^\d+/g, (m) => m.replace(/(?=(?!^)(\d{3})+$)/g, ','))
    },
    receive:debounce(async function(){
      if(!this.status) return this.$toast(this.$t('airdrop.notStart'))
      const { data, status } = await USER_API.getAirDrop({
        address: window.ethereum.selectedAddress,
        lang: this.local.locale
      });
      if (status == 1) {
        return this.$dialog.alert({ message: data,confirmButtonText: this.$t('CbkbExchange.know') ,confirmButtonColor: '#7524f9' });
      }
      this.$dialog.alert({ message: data,confirmButtonText: this.$t('CbkbExchange.know'),confirmButtonColor: '#7524f9' }).then(() =>{
        this.getAirDropCount(window.ethereum.selectedAddress);
      });
    }),
    viewAll(){
      this.flag = !this.flag;
      let dom =document.getElementsByClassName('about-con')[0];
      if(this.flag){
        dom.setAttribute('style','-webkit-line-clamp:10')
      }else{
        dom.setAttribute('style','-webkit-line-clamp:2')
      }
    }
  }
}
